body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

::-webkit-scrollbar {
  width: 8px;
  float: right;
}

::-webkit-scrollbar-track {
  background-color: rgb(32, 32, 32);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(226, 226, 226);
  border-radius: 4px;
}

::-webkit-scrollbar-button {
  display: none;
}

img {
  pointer-events: none;
}

.app {
  display: flex;
  position: relative;
  flex-wrap: nowrap;
  width: 100vw;
  height: 100vh;
}

#map {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
}

#sidebar {
  width: 25vw;
  min-width: 300px;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(55, 57, 61);
  overflow-y: auto;
}

#sidebar h1 {
  margin-left: 20px;
  color: white;
}

input {
  margin: 5px;
  margin-left: 0;
  margin-right: 8px;
  padding-left: 3px;
  width: 40%;
}

.form-container {
  border-radius: 15px;
  background-color: rgb(226, 226, 226);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4);
  width: 20vw;
  padding: 10px;
  font-size: 14px;
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.form-container p {
  margin: 0;
}

.rounded {
  cursor: pointer;
  border: none;
  margin-top: 10px;
  border-radius: 14px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 6px;
  padding-bottom: 6px;
  color: white;
  background-color: rgb(26, 206, 26);
  outline: none;
}

.rounded:hover {
  background-color: rgb(20, 167, 20);
}

.blank {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  padding-top: 3px;
  padding-bottom: 0;
}

.collapse {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 6px;
  padding-top: 8px;
  color: rgb(26, 21, 21);
  background-color: rgb(226, 226, 226);
  outline: none;
  margin-left: 20px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4);
}

#reset {
  background-color: red;
  position: absolute;
  right: 20px;
  bottom: 20px;
}

#snap {
  background-color: rgb(25, 167, 211);
  margin-left: 10px;
}

#snap:hover {
  background-color: rgb(14, 136, 173);
}

#pop {
  background-color: orange;
  margin-left: 10px;
}

#pop:hover {
  background-color: darkorange;
}

#reset:hover {
  background-color: darkred;
}

#toggle {
  position: absolute;
  right: 20px;
  bottom: 60px;
  z-index: 100;
}

#raw {
  color: rgb(23, 24, 24);
  background-color: rgb(246, 246, 246);
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 15px;
  border-radius: 10px;
  border: none;
  height: 35vh;
  width: 89%;
  outline: none;
  overflow-y: auto;
}

.output {
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

#close {
  position: absolute;
  right: 0;
  padding: 0;
}

.line {
  background-color: rgb(226, 226, 226);
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  right: 15px;
  top: 180px;
  height: 28px;
}